.upf-content--gallery {
  position: relative;
}

.upf-content__gallery-image {
  display: block;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  width: 100%;
}

.upf-content__gallery-image:not(:first-child) {
  left: 0;
  position: absolute;
  top: 0;
}

.upf-content__gallery-image.active {
  opacity: 1;
}

